import Promise from 'bluebird';
import atgCommerce from './_base';
import bus from '../../bus';
import { DOM, format, log } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/* BEHAVIOUR
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support:  no
+ Free shipping:  no
+ ‘isValid’ implemented:  no
+ Codes can be replaced by another code:  no
+ Pages, where extension works: billing page
+ Is driver SPA: no
+ Gather codes on site pages: no
 * Can getExistingCodes only from cart page
 * Can remove only if we have remove url from cart page
 * Rewrote all methods, exсept _parseTotal, because driver doesn't work as a base
 */

const BASE_URL = 'https://www.barneyswarehouse.com';
const CART_URL = `${BASE_URL}/cart`;
const BASE_REQUEST_URL = `${BASE_URL}/checkout/gadgets`;
const BILLING_URL = `${BASE_URL}/checkout/billing`;

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();
		this.APPLIED_CODES_SELECTOR = '.cartPromoCodeTitledesc';
		this.TOTAL_SELECTOR = '.atg_store_orderTotal .atg_store_orderSummaryItem';
		this.PRICE_BLOCK_SELECTOR = '.atg_store_orderSubTotals li';
		this.PRICE_SELECTOR = '.atg_store_orderSummaryItem';
		this.REMOVE_CODE_ELEMENT = '.promoCodeRemoveLink a';
		this.cartPageHTML = null;
	}

	getExistingCodes() {
		return this._getCartPage().then(responseHTML => {
			const codes = [...extractElement(this.APPLIED_CODES_SELECTOR, responseHTML, true)]
				.filter(Boolean)
				.map(code => code.textContent.trim());
			// if we have exist code,we will have response, from which we can get url for remove
			this.cartPageHTML = responseHTML;
			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	removeCodes(codes) {
		return this._removeCode(codes[0], this.cartPageHTML)
			.then(() => this._getOrderSummary())
			.then(responseString => this._parseTotal(responseString));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(() => this._getOrderSummary())
				.then(response => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
					};

					result.finalPrice = this._parseTotal(response);
					result.finalDiscount = originalPrice - result.finalPrice;

					if (result.finalPrice < originalPrice) {
						return this._getCartPage().then(responseHTML =>
							this._removeCode(code, responseHTML).then(() => bus.$emit('code-checked', result)),
						);
					}
					return bus.$emit('code-checked', result);
				}),
		);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getOrderSummary().then(response => {
			const shipping = elemToFloat(this._findElement('Shipping', response)) || 0;
			const tax = elemToFloat(this._findElement('Tax', response)) || 0;
			return this._parseTotal(response) - shipping - tax;
		});
	}

	_findElement(targetString, response) {
		return [...extractElement(this.PRICE_BLOCK_SELECTOR, response, true)]
			.find(el => el.textContent.includes(targetString))
			.querySelector(this.PRICE_SELECTOR);
	}

	_removeCode(code, html) {
		return this._manageCode(this._getRemoveRequestData(code, html));
	}

	_applyCode(code) {
		return this._manageCode(`${BASE_REQUEST_URL}/ajaxPromotionApply.jsp?couponCode=${code}`);
	}

	_manageCode(url) {
		return fetch(url, {
			method: 'POST',
			credentials: 'include',
		});
	}

	_getCartPage() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_getOrderSummary() {
		return fetch(`${BASE_REQUEST_URL}/ajaxCheckoutOrderSummary.jsp`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_getRemoveRequestData(code, html) {
		return [...extractElement(this.REMOVE_CODE_ELEMENT, html, true)]
			.map(element => element.href)
			.find(url => url && url.includes(code));
	}
};

export default {
	name: 'Barneys Warehouse',
	domain: 'barneyswarehouse.com',
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(BILLING_URL);
	},
	isCartEmpty() {
		return (
			(document.querySelector('.atg_store_orderSummaryItem') &&
				elemToFloat(document.querySelector('.atg_store_orderSummaryItem')) === 0) ||
			(document.querySelector('#atg_store_sessionExpired') || {}).offsetParent
		);
	},
};
