import atgCommerce from './_base';
import { extractElement } from '../../../utils/DOM';
import { elemToFloat } from '../../../utils/format';

// Cart page has promo input, but on cart  page we can apply only one first code, and can`t delete code
// also we can`t get total price from page
// We can send request on checkout page, but there price includes taxes and shipping. On cart page user can`t see such details.
// As a result the discount and final price will not be accurate
/**
 * Driver behavior description:
 -    Existing code: yes
 -    Remove code: no
 -    Stackable: no
 -    Free gift support: no
 -    ‘isValid’ implemented: no
 Optional:
 -    Show message about free shipping on the cart page (without discount info): no
 -    Is driver SPA and add the condition to show popup:  no
 -	  test cides in the 'https://www.teleflora.com/checkout/billing_review.jsp'
 */
const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '.rmveCoupnCde';
		this.APPLIED_CODES_ATTRIBUTES = 'data-ccode';
		this.TOTAL_SELECTOR = '.m-billing-review-grand-total';

		this.APPLY_CODE_FORM_SELECTOR = '#couponCodeForm, #couponCartForm';
		this.REMOVE_CODE_FORM_SELECTOR = '#couponCodeRemoveForm';

		// Redefine in driver in case it differs if we need to add some additional body params
		this.APPLY_REQUEST_BODY_DATA = {
			couponCodeBtn: 'Submit',
		};
		this.REMOVE_REQUEST_BODY_DATA = {
			couponCodeRemoveBtn: 'Submit',
		};

		this.APPLY_CODE_PARAM_NAME = 'orgCouponCode,couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		// Redefine in driver in case they are needed
		this.REQUEST_HEADERS = {
			Accept: 'text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8',
			'Content-Type': 'application/x-www-form-urlencoded',
		};

		this.CHARGES_REGEX = /(shipping|tax|service fee)/i;
		this.CHARGES_SELECTOR = '.m-billing-item span.m-billing-price';
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this._getUpdatedCart();
		const total = this._parseTotal(cartStr);
		const taxes = [...extractElement(this.CHARGES_SELECTOR, cartStr, true)]
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(elemToFloat)
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0); // fee & tax might not be counted on page
		return total - taxes;
	}

	_getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'teleFlora',
	domain: 'teleflora.com',
	Driver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.teleflora.com/checkout/billing_review.jsp');
	},
};
