import atgCommerce from './_base';
import { format, DOM } from '../../../utils';
import bus from '../../bus';

const { elemToFloat } = format;
const { extractFormValues } = DOM;

/**
 * We can apply one code
 * We can remove code
 * We can apply code only on https://www.uscellular.com/uscellular/order/ncsOrderRecap.jsp page
 *
 * Special behevior:
 * We can add product like prepaid item or like installment price.
 * On checkout page total price changed from 0 only with prepaid price.
 * With Installment pricing on checkout page we can see total by monthly paymant.
 * _parseTotal checks if totalPrice equals 0 then we check monthly totalPrice.
 */

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();
		this.BASE_URL = 'https://www.uscellular.com';

		this.APPLIED_CODES_SELECTOR = '#applied-coupon';
		this.TOTAL_SELECTOR = '.due-today-value .price';
		this.TOTAL_BY_MONTH_SELECTOR = '.total-due-monthly-line .price';
		this.INPUT_APPLY_CODE_SELECTOR = '[id=coupon-form]';
		this.APPLIED_CODES_PATT = /Remove "(\w+)"/;

		this.MANAGE_CODE_FORM = extractFormValues(this.INPUT_APPLY_CODE_SELECTOR);
	}

	watchForCartUrl() {
		let lastState = false;
		setInterval(() => {
			const totalPrice = document.querySelector(this.TOTAL_SELECTOR) ? this._parseTotal() : 0;
			const isOnRightUrl = window.location.href.startsWith(`${this.BASE_URL}/uscellular/order/ncsOrderRecap.jsp`);

			const currentState = isOnRightUrl && totalPrice;

			if (lastState !== currentState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			lastState = currentState;
		}, 500);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.filter(el => Boolean(el.innerText))
			.map(el => el.innerText.match(this.APPLIED_CODES_PATT)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	_removeCode(code) {
		return this._manageCode(code, 'remove');
	}

	_applyCode(code) {
		return this._manageCode(code);
	}

	_manageCode(code, action = 'add') {
		const codeParameter = `{"couponCode":"${code}","couponAction":"${action}"}`;

		this.MANAGE_CODE_FORM.params.set('formContent', codeParameter);

		return fetch(this.MANAGE_CODE_FORM.action, {
			method: 'POST',
			credentials: 'include',
			body: this.MANAGE_CODE_FORM.params,
		})
			.then(response => response.text())
			.then(response => JSON.parse(response));
	}

	/**
	 *  _parseTotal checks if totalPrice equals 0 then we check monthly totalPrice.
	 * @param {Object}
	 */
	_parseTotal(data) {
		let totalPrice = null;
		if (data) {
			const totalToday = data.totalDueTodaySummary.totalDueToday;
			totalPrice = totalToday > 0 ? totalToday : data.totalDueMonthlySummary.monthlyChargeSummary;
		} else {
			const totalElement = document.querySelector(this.TOTAL_SELECTOR);
			if (elemToFloat(totalElement) > 0) {
				totalPrice = elemToFloat(totalElement);
			} else {
				totalPrice = elemToFloat(document.querySelector(this.TOTAL_BY_MONTH_SELECTOR));
			}
		}

		return totalPrice;
	}
};

export default {
	name: 'United States Cellular Corp.',
	domain: 'uscellular.com',
	isSinglePageApp: true,
	Driver,
	isOnCartUrl() {
		return true;
	},
};
