import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElementFromHTML, extractFormValues } = DOM;

export default class atgCommerce extends BaseDriver {
	constructor() {
		super();
		// Should be defined in driver
		this.APPLIED_CODES_SELECTOR = '';
		this.APPLIED_CODES_ATTRIBUTES = '';
		this.TOTAL_SELECTOR = '';

		// Should be defined in driver
		this.APPLY_CODE_FORM_SELECTOR = '';
		this.REMOVE_CODE_FORM_SELECTOR = '';

		// Redefine in driver in case it differs
		this.APPLY_REQUEST_BODY_DATA = {};
		this.REMOVE_REQUEST_BODY_DATA = {};

		// Redefine in driver in case it differs
		// if you have couple param names with code, just write them through a comma and without space, like 'param1,param2,param3'
		this.APPLY_CODE_PARAM_NAME = '';
		this.REMOVE_CODE_PARAM_NAME = '';

		// Redefine in driver in case they are needed
		this.REQUEST_HEADERS = {};

		// Redefine in driver in case it differs
		this.isStackable = false;

		this.lastResponse = null;
	}

	// Base Driver methods
	getExistingCodes() {
		// This method may vary from merchant to merchant
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(code =>
			code.getAttribute(this.APPLIED_CODES_ATTRIBUTES),
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		let lastResponse = null;
		return Promise.each(codes, code =>
			this._removeCode(code).then(responseHTML => {
				lastResponse = responseHTML;
				return Promise.resolve();
			}),
		).then(() => this._parseTotal(lastResponse));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		const workingDiscounts = [];

		return Promise.each(codes, code =>
			this._applyCode(code).then(response => {
				const result = { code, finalPrice: originalPrice, finalDiscount: 0 };

				result.finalPrice = this._parseTotal(response);
				result.finalDiscount = originalPrice - result.finalPrice;

				if (result.finalPrice < originalPrice) {
					workingDiscounts.push(result);

					return this._removeCode(code).then(() => {
						bus.$emit('code-checked', result);
						return Promise.resolve();
					});
				}
				return bus.$emit('code-checked', result);
			}),
		).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
				});
			}
			return Promise.resolve();
		});
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice } = validationInfo;
		workingDiscounts.sort((a, b) => b.finalDiscount - a.finalDiscount);
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];
		return Promise.each(workingDiscounts, discount =>
			this._applyCode(discount.code).then(responseText => {
				const finalPrice = this._parseTotal(responseText);
				const finalDiscount = originalPrice - finalPrice;
				bus.$emit('code-stacked', { code: discount.code, finalPrice, finalDiscount });
				if (finalPrice < currentPrice) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					return Promise.resolve();
				}
				return this._removeCode(discount.code);
			}),
		).then(() => {
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			});
			return Promise.resolve();
		});
	}

	applyCodes(codes) {
		return this.isStackable ? Promise.resolve() : this._applyCode(codes[0]);
	}

	// ATG Commerce Driver methods
	_removeCode(code) {
		// Add to body parametr with code for removing
		if (this.REMOVE_CODE_PARAM_NAME) {
			const codeArray = this.REMOVE_CODE_PARAM_NAME.split(',');
			codeArray.forEach(param => this._getRemoveData().params.set(param, code));
			// this._getRemoveData().params.set(this.REMOVE_CODE_PARAM_NAME, code);
		}

		return this._manageCode(this._getRemoveData(), this.REQUEST_HEADERS);
	}

	_getRemoveData() {
		if (!this.removeCodeData) {
			this.removeCodeData = extractFormValues(this.REMOVE_CODE_FORM_SELECTOR, this.lastResponse);
			// Add to body params data, that user set
			Object.entries(this.REMOVE_REQUEST_BODY_DATA).forEach(([key, value]) => {
				this.removeCodeData.params.set(key, value);
			});
		}
		return this.removeCodeData;
	}

	_getApplyData() {
		if (!this.applyCodeData) {
			this.applyCodeData = extractFormValues(this.APPLY_CODE_FORM_SELECTOR, this.lastResponse);
			// Add to body params data, that user set
			Object.entries(this.APPLY_REQUEST_BODY_DATA).forEach(([key, value]) => {
				this.applyCodeData.params.set(key, value);
			});
		}
		return this.applyCodeData;
	}

	_applyCode(code) {
		const codeArray = this.APPLY_CODE_PARAM_NAME.split(',');
		codeArray.forEach(param => this._getApplyData().params.set(param, code));

		return this._manageCode(this._getApplyData(), this.REQUEST_HEADERS);
	}

	_manageCode(bodyData, headersData) {
		return fetch(bodyData.action, {
			method: 'POST',
			credentials: 'include',
			body: bodyData.params,
			headers: headersData,
		})
			.then(response => response.text())
			.then(response => {
				// Return JSON if it is possible
				try {
					this.lastResponse = JSON.parse(response);
				} catch (e) {
					this.lastResponse = response;
				}
				return this.lastResponse;
			});
	}

	_parseTotal(data) {
		let totalElement = null;
		if (data) {
			totalElement = extractElementFromHTML(data, this.TOTAL_SELECTOR);
		} else {
			totalElement = document.querySelector(this.TOTAL_SELECTOR);
		}
		return elemToFloat(totalElement);
	}
}
