import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { format, DOM, log, browserUtils } from '../../../utils';

const { elemToFloat, toFloat, toInt } = format;
const { extractElement, extractFormValues } = DOM;
const { getCookie, setCookie } = browserUtils;

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: no
•    Free gift support: no
•    ‘isValid’ implemented: yes
•    Manually apply code: no
•    active page: Cart
•    maxCoupons: 6
Optional:
•    Show message about free shipping on the cart page (without discount info): no
•    Is driver SPA and add the condition to show popup: yes
•    Gather codes on site pages: yes
============Special case==========
Checkout page is not supported due to redirection to the Cart page
after final code applying (cannot avoid cart),
also there are issue with removing code
 also we can run contesting only 4 times in 40 minutes, due to site behavior that return 429 error status */

const BASE_URL = 'https://www.shoemall.com';
const CART_URL = `${BASE_URL}/cart/cart.jsp`;

const Driver = class Driver extends BaseDriver {
	constructor() {
		super();
		this.APPLIED_CODES_SELECTOR = '[name$=couponCode]';
		this.TOTAL_SELECTOR = '.total.bold span:last-child';
		this.SESSION_SELECTOR = '.SessionExpired';
		this.FORM_DATA_SELECTOR = '#couponform, #applyCouponForm';
		this.APPLY_URL = `${BASE_URL}/cart/gadgets/?_DARGS=/cart/gadgets/promotion.jsp`;
		this.INVALID_CODE_SELECTOR = '#atg_store_promotionCodeInput_error, #promotionCodeInput_error';
		this.REMOVE_BUTTON_SELECTOR = 'a[title="remove"], .successPromoCoupon a';
		this.EMPTY_CART_EL_SELECTOR = '.emptycart-item-message';
		this.ERROR_EL_SELECTOR = '.cf-error-overview';
		this.lastResponse = null;
		this.PAGE_CONTENT_SELECTOR = '.page-content';

		this.MERCHANT_PROMO_BLOCK_SELECTOR = '.SM-sale-banner-promo-code b';
		this.SUMMERY_BLOCK_SELECTOR = '#orderSummary .clearfix';
		this.SUMMERY_VALUE = '.regular-text';
	}

	checkCondition() {
		const { href } = window.location;
		const isCartEmpty = document.querySelector(this.EMPTY_CART_EL_SELECTOR);
		const errorEl = document.querySelector(this.ERROR_EL_SELECTOR);
		const sessionExpired = document.querySelector(this.SESSION_SELECTOR);
		const isRightUrl = href.startsWith(CART_URL);
		const isCorrectPage = document.title && !document.title.includes('Site Under Maintenance');

		if (!getCookie('numberOfTesting')) {
			setCookie('numberOfTesting', 1, 40);
		}

		return (
			isRightUrl &&
			!errorEl &&
			!isCartEmpty &&
			!sessionExpired &&
			isCorrectPage &&
			toInt(getCookie('numberOfTesting')) < 4
		);
	}

	getMerchantCodes() {
		return this._getUpdatedCart().then(response => {
			return [...extractElement(this.MERCHANT_PROMO_BLOCK_SELECTOR, response, true)]
				.filter(Boolean)
				.map(code => code.innerText);
		});
	}

	getExistingCodes() {
		const codes = new Set();
		if (document.querySelector(this.PAGE_CONTENT_SELECTOR)) {
			if (document.querySelector(this.REMOVE_BUTTON_SELECTOR)) {
				codes.add(
					...[...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
						.map(element => element.getAttribute('value'))
						.filter(Boolean),
				);
			}
		}
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes() {
		return this._removeCode()
			.then(() => this._getUpdatedCart())
			.then(responseHTML => {
				if (extractElement(this.PAGE_CONTENT_SELECTOR, responseHTML)) {
					this.lastResponse = responseHTML;
					return this._parseTotal(responseHTML);
				}
				return this._parseTotal();
			});
	}

	beforeCheckCodes() {
		const numberOfTesting = toInt(getCookie('numberOfTesting'));
		setCookie('numberOfTesting', numberOfTesting + 1, 45);

		this.FORM_DATA = extractFormValues(this.FORM_DATA_SELECTOR, this.lastResponse);
		Promise.resolve();
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return Promise.delay(3000).then(() =>
				this._applyCode(code)
					.then(() => this._getUpdatedCart())
					.then(responseHTML => {
						if (this._isCodeInvalid(responseHTML)) {
							result.isValid = false;
							return bus.$emit('code-checked', result);
						}
						result.finalPrice = this._parseTotal(responseHTML);
						result.finalDiscount = originalPrice - result.finalPrice;
						return bus.$emit('code-checked', result);
					}),
			);
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			if (extractElement(this.PAGE_CONTENT_SELECTOR, response)) {
				const shippingElem = this._findElement('Shipping', response);
				const taxElem = this._findElement('Tax', response);
				const tax = taxElem ? toFloat(taxElem) : 0;
				const shipping = shippingElem ? toFloat(shippingElem) || 0 : 0;
				return this._parseTotal(response) - tax - shipping;
			}
			return 0;
		});
	}

	_findElement(string, response) {
		return [...extractElement(this.SUMMERY_BLOCK_SELECTOR, response, true)]
			.filter(el => el.innerText.includes(string))
			.map(el => el.querySelector(this.SUMMERY_VALUE).innerText)[0];
	}

	_removeCode() {
		const removeURlPart = document.querySelector(this.REMOVE_BUTTON_SELECTOR).getAttribute('href');
		return fetch(`${BASE_URL}${removeURlPart}`, {
			method: 'GET',
			credentials: 'include',
			redirect: 'manual',
		});
	}

	_applyCode(code) {
		if (this.FORM_DATA) {
			this.FORM_DATA.params.set('/atg/store/order/purchase/CouponFormHandler.couponCode', code);
			this.FORM_DATA.params.set('/atg/store/order/purchase/CouponFormHandler.claimCoupon', 'submit');
			return fetch(this.APPLY_URL, {
				method: 'POST',
				credentials: 'include',
				body: this.FORM_DATA.params,
				redirect: 'manual',
			});
		}
		return Promise.resolve();
	}

	_getUpdatedCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_isCodeInvalid(data) {
		return (
			Boolean(extractElement(this.INVALID_CODE_SELECTOR, data)) ||
			!extractElement(this.PAGE_CONTENT_SELECTOR, data)
		);
	}

	_parseTotal(data = false) {
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
	}
};

export default {
	Driver,
	name: 'Shoemall',
	domain: 'shoemall.com',
	maxCoupons: 6,
	isSinglePageApp: true,
};
