import Promise from 'bluebird';
import atgCommerce from './_base';
import bus from '../../bus';
import { DOM, completionActions, log, format } from '../../../utils';
import { sessionTimer } from '../../lib';

const { extractElement, extractData } = DOM;
const { elemToFloat } = format;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: yes (some codes)
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(SIGN IN / GUEST CHECKOUT, PAYMENT, REVIEW AND PLACE ORDER)
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ Special case about the driver works:
	- sessionTimer yes (30min)
	- if user apply one code(002822), after that, without deleting,  apply another code(050686), the first one doesn`t delete, it will save for later
      if we delete one code, another code, that was saved for later, will be applied again
	  but this code will be shown after go to Checkout and go back to Cart (if you are on Cart page) or refresh page (if you are on Checkout page),
	  so after all type of manipulation get response from Checkout and work with it
	- get existing codes from Checkout page
	- don't support Shipping, because after reloading we lose information
+ Credentials (josephhhhh@gmail.com, 12345qwertyQ)
*/

const BASE_URL = 'https://www.rue21.com/store/checkout';
const CHECKOUT_URL = `${BASE_URL}/singlePageCheckout.jsp`;

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '#coupounRemItemId0';
		this.APPLIED_CODES_ATTRIBUTES = 'value';
		this.TOTAL_SELECTOR = '#order-total';
		this.VALID_CODE_SELECTOR = '.promo-codes-applied .promo-name';
		this.APPLY_CODE_FORM_SELECTOR = '#couponForm';
		this.REMOVE_CODE_FORM_SELECTOR = '#couponRemoveForm';

		this.APPLY_REQUEST_BODY_DATA = {
			'/atg/commerce/promotion/CouponFormHandler.claimCoupon': 'apply',
		};

		this.APPLY_CODE_PARAM_NAME = '/atg/commerce/promotion/CouponFormHandler.couponClaimCode';
		this.REMOVE_CODE_PARAM_NAME = '/atg/commerce/promotion/CouponFormHandler.unClaimCoupon';
		this.SHIPPING_TYPE_PARAM_NAME = '/atg/commerce/order/purchase/ShippingGroupFormHandler.shippingMethod';

		this.REQUEST_HEADERS = {
			accept: 'text/html, */*',
		};

		this.CODE_INPUT_SELECTOR = '.promo-code-text-input';

		this.EMPTY_CART_SELECTOR = '.cart-empty-message';
		this.REMOVE_CODE_BUTTON_SELECTOR = '.removeCouponFromCart';
		this.CHECKOUT_STEPS_SELECTOR = '.mainContainer';

		this.SHIPPING_SELECTOR = '.ship-handling .summary-value';
		this.TAX_SELECTOR = '.checkoutTax .summary-value';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 30,
			toCheckDOM: true,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isOnCartUrl = href.startsWith(`${BASE_URL}/cart.jsp`);
		const isCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const checkoutStepsEl = document.querySelector(this.CHECKOUT_STEPS_SELECTOR);
		const isOnRightCheckoutStep = checkoutStepsEl ? this._checkCheckoutStep(checkoutStepsEl) : false;
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const currentState = (isOnCartUrl || isOnRightCheckoutStep) && !isCartEmpty && isCodeInput;
		return currentState;
	}

	getExistingCodes() {
		// if user apply one code, after that, without deleting,  apply another code, the first one doesn`t delete, it will save for later
		// if we delete one code, another code, that was saved for later, will be applied again
		// but this code will be shown after go to Checkout and go back to Cart (if you are on Cart page) or refresh page (if you are on Checkout page),
		return this._getUpdatedCheckout().then(responseText => {
			const codes = [...extractElement(this.APPLIED_CODES_SELECTOR, responseText, true)].map(code =>
				code.getAttribute(this.APPLIED_CODES_ATTRIBUTES),
			);
			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	// if user apply one code, after that, without deleting,  apply another code, the first one doesn`t delete, it will save for later
	// if we delete one code, another code, that was saved for later, will be applied again
	// we need to remove all of code while btn `remove` present on the page
	removeCodes(codes) {
		let lastResponse = null;
		let currentCode = codes[0];
		const arr = [...new Array(15)];

		return Promise.each(arr, () => {
			if (currentCode) {
				return this._removeCode(currentCode)
					.then(() => this._getUpdatedCheckout())
					.then(responseText => {
						const removeCodeBtnSelector = extractElement(this.REMOVE_CODE_BUTTON_SELECTOR, responseText);
						currentCode = removeCodeBtnSelector
							? extractData(this.APPLIED_CODES_SELECTOR, responseText, null, 'value')
							: null;
						lastResponse = responseText;
						return Promise.resolve();
					});
			}
			return Promise.reject(new Error('All codes removed'));
		}).catch(error => {
			if (error.message !== 'All codes removed') {
				throw error;
			}
			return this._parseTotal(lastResponse);
		});
	}

	beforeCheckCodes(originalPrice) {
		if (originalPrice) {
			return originalPrice;
		}
		// need to get Checkout page to get the right total price
		return this._getUpdatedCheckout().then(responseText => this._parseTotal(responseText));
	}

	checkCodes(codes, originalPrice) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(() => this._getUpdatedCheckout())
				.then(response => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
					};
					result.isValid = Boolean(extractElement(this.VALID_CODE_SELECTOR, response));
					if (!result.isValid) {
						return bus.$emit('code-checked', result);
					}
					result.finalPrice = this._parseTotal(response);
					result.finalDiscount = originalPrice - result.finalPrice;

					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				}),
		);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCheckout().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const tax = extractElement(this.TAX_SELECTOR, response);
			const taxValue = tax ? elemToFloat(tax) : 0;
			return this._parseTotal(response) - shipping - taxValue;
		});
	}

	_getUpdatedCheckout() {
		return fetch(CHECKOUT_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_checkCheckoutStep(checkoutStepsEl) {
		const currentStep = checkoutStepsEl.getAttribute('current-step');
		const isOnShipping = currentStep === 'shipping';
		return !isOnShipping;
	}
};

export default {
	name: 'rue21',
	domain: 'rue21.com',
	isSinglePageApp: true,
	Driver,
	completionAction: completionActions.revisit,
};
