import atgCommerce from './_base';
import { DOM, completionActions, request, requestTypes } from '../../../utils';
import { sessionTimer, backgroundMessaging } from '../../lib';

const { extractElementFromHTML, extractFormValues } = DOM;

/*
Driver behavior:
•   Existing code: yes
•   Remove code: yes
•   Code retesting: yes
•   Stackable: no
•   Free gift support: no
•   isValid implemented: no
•   Manually apply code: no
•   Show message about free shipping: no
•   Driver is SPA: yes, on cart page if code input present and no forbid testing cookie present
•   Gather merchant codes: yes
•   Special case: testing is forbidden for 5 minutes after last testing toa void block
•   sessionTimer: 8min
*/

const DOMAIN = 'autozone.com';
const CART_URL = `https://www.${DOMAIN}/cart/cart.jsp`;

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '.promo-code';
		this.TOTAL_SELECTOR = '.order-subtotal-section .subtotal-price';
		this.CODE_REGEX = /\w+/;

		this.APPLY_CODE_FORM_SELECTOR = '#applyCouponForm';
		this.REMOVE_CODE_FORM_SELECTOR = '#removeCouponForm';

		this.APPLY_CODE_PARAM_NAME = '/atg/commerce/promotion/CouponFormHandler.couponClaimCode';

		this.REMOVE_ID_CONTAINER_SELECTOR = '#removeCouponForm .promo-code-box';
		this.REMOVE_ID_SELECTOR = '.submit-link.remove-coupon';

		this.ROBOTS_SELECTOR = 'META[NAME*="ROBOTS"]';
		this.COUPON_FIELD_SELECTOR = '#promoCodeInput';

		this.EMPTY_SELECTOR = '#noItems';

		this.MERCHANT_CODES_SELECTOR = '#globalHeader a>img';
		this.MERCHANT_CODES_REGEX = /code:\s(\w+)/i;

		this.DISABLE_TIME_MS = 5 * 60 * 1000;

		request.setType(requestTypes.embedded);
		this.isNotMaxCodeTesting = true;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 8,
		});
	}

	async checkCondition() {
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				this.isNotMaxCodeTesting = true;
				backgroundMessaging.deleteDriverStorage(DOMAIN);
			} else {
				this.isNotMaxCodeTesting = false;
			}
		}
		const emptyCart = document.querySelector(this.EMPTY_SELECTOR);
		const couponInput = document.querySelector(this.COUPON_FIELD_SELECTOR);
		const onCart = window.location.href.startsWith(CART_URL);
		return this.isNotMaxCodeTesting && onCart && !emptyCart && couponInput && couponInput.offsetParent;
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.MERCHANT_CODES_SELECTOR)]
			.map(elem => elem.alt.match(this.MERCHANT_CODES_REGEX))
			.filter(Boolean)
			.map(elem => elem[1]);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(el => el.innerText.match(this.CODE_REGEX))
			.filter(Boolean)
			.map(match => match[0]);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeApplyCodes() {
		this.isNotMaxCodeTesting = false;
		backgroundMessaging.setDriverStorage(DOMAIN, { lastTestTimestamp: Date.now() });
	}

	applyCodes(codes) {
		return super._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return fetch(window.location.href)
			.then(response => response.text())
			.then(responseHTML => this._parseTotal(responseHTML));
	}

	_manageCode(bodyData) {
		return request
			.get(`${CART_URL}?${bodyData.params}`)
			.then(response => response.text())
			.then(response => {
				try {
					this.lastResponse = JSON.parse(response);
				} catch (e) {
					this.lastResponse = response;
				}
				return this.lastResponse;
			});
	}

	_removeCode(code) {
		// Add to body parameters with code for removing
		if (this.REMOVE_CODE_PARAM_NAME) {
			const codeArray = this.REMOVE_CODE_PARAM_NAME.split(',');
			codeArray.forEach(param => this._getRemoveData(code).params.set(param, code));
		}

		return this._manageCode(this._getRemoveData(code), this.REQUEST_HEADERS);
	}

	_getRemoveData(code) {
		if (!this.removeCodeData) {
			this.removeCodeData = extractFormValues(this.REMOVE_CODE_FORM_SELECTOR, this.lastResponse);
			// Add to body params data, that user set
			Object.entries(this.REMOVE_REQUEST_BODY_DATA).forEach(([key, value]) => {
				this.removeCodeData.params.set(key, value);
			});
		}
		const promotionId = this.getCodeId(code, this.lastResponse);
		this.removeCodeData.params.set('/atg/commerce/promotion/CouponFormHandler.promotionId', promotionId);
		return this.removeCodeData;
	}

	getCodeId(code, html) {
		let promotionId = null;
		const containers = html
			? extractElementFromHTML(html, this.REMOVE_ID_CONTAINER_SELECTOR, true)
			: document.querySelectorAll(this.REMOVE_ID_CONTAINER_SELECTOR);

		// active promo code always is in the first line so if code we want to delete isn't in first line
		// there is no id on the page (It is possible to remove only active code)
		if (containers.length > 0 && containers[0].innerText.includes(code)) {
			const promotionElement = html
				? extractElementFromHTML(html, this.REMOVE_ID_SELECTOR)
				: document.querySelector(this.REMOVE_ID_SELECTOR);
			[, promotionId] = promotionElement.getAttribute('onclick').split("'");
		}
		return promotionId;
	}

	_parseTotal(data) {
		if (data) {
			const robotsPage = extractElementFromHTML(data, this.ROBOTS_SELECTOR);
			if (robotsPage) {
				return Number.MAX_SAFE_INTEGER;
			}
		}
		return super._parseTotal(data);
	}
};

export default {
	name: 'AutoZone',
	domain: 'autozone.com',
	maxCoupons: 2, // due to block
	completionAction: completionActions.revisit,
	isSinglePageApp: true,
	Driver,
};
