import Promise from 'bluebird';
import atgCommerce from './_base';
import bus from '../../bus';
import { DOM, format } from '../../../utils';

const { extractElementFromHTML, extractElement } = DOM;
const { elemToFloat } = format;

/**
Driver behavior:
•   Existing code: yes
•   Remove code: no
•   Code retesting: no, can't remove codes
•   Stackable: no
•   Free gift support: no
•   isValid implemented: no
•   Show message about free shipping: no
•   Driver is SPA: no, works on cart & checkout-review
•   Gather merchant codes: no
•   Special case: When we have only one working code and try to test a couple of codes the site will behave like stackable but it isn't. Only one of them will give a discount. If we write stackCodes() method, the driver could choose `fake  code` like the best code
•   Credentials: no
•   Test Item(s): https://www.horchow.com/Lauren-Ralph-Lauren-Sanders-Matching-Items/cprod146020012/p.prod
•   Valid Code(s): -, last checked 03-25-2019
*/

const BASE_URL = 'https://www.horchow.com';

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '.promo.desc';
		this.APPLIED_CODES_REGEXP = /applied:\s([a-aA-Z0-9,]*)/i;
		this.TOTAL_SELECTOR = '#txt_orderTotal';

		this.APPLY_CODE_FORM_SELECTOR = '#promo-form';

		this.APPLY_REQUEST_BODY_DATA = {
			'/nm/formhandler/checkout/CartFormHandler.applyPromoCode': 'Apply',
		};

		this.APPLY_CODE_PARAM_NAME = '/nm/formhandler/checkout/CartFormHandler.promoCode';

		this.CHARGES_SELECTORS = ['#txt_giftPackingAmount', '#txt_deliveryAndProcessingCharges', '#txt_taxAmount'];
	}

	getExistingCodes() {
		const codesOnPage = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(code => code.innerText.match(this.APPLIED_CODES_REGEXP))
			.filter(Boolean);

		const codes = codesOnPage.length === 0 ? [] : codesOnPage.map(match => match[1].split(','))[0];
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return Promise.resolve();
	}

	checkCodes(codes) {
		const originalPrice = this._parseTotal();

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};

			return this._applyCode(code)
				.then(() => this._getSummary())
				.then(responseHTML => {
					if (!extractElementFromHTML(responseHTML, '.BadPromoCode')) {
						result.finalPrice = this._parseTotal(responseHTML);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.isValid = result.finalPrice < originalPrice;

						bus.$emit('code-checked', result);
						result.originalPrice = originalPrice;
						return bus.$emit('send-validation-codes', result);
					}
					bus.$emit('code-checked', result);
					result.originalPrice = originalPrice;
					return bus.$emit('send-validation-codes', result);
				});
		});
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this._getSummary();
		const total = this._parseTotal(cartStr);
		const taxes = this.CHARGES_SELECTORS.map(chargeSelector => extractElement(chargeSelector, cartStr))
			.filter(Boolean)
			.map(chargeElement => elemToFloat(chargeElement))
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0); // shipping & tax might not be counted on cart
		return total - taxes;
	}

	_getSummary() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_manageCode(bodyData, headersData) {
		return fetch(bodyData.action, {
			method: 'POST',
			credentials: 'include',
			body: bodyData.params,
			headers: headersData,
			redirect: 'manual',
		});
	}
};

export default {
	name: 'Horchow',
	domain: 'horchow.com',
	Driver,
	validationSupport: true,
	isOnCartUrl(href) {
		return (
			href.startsWith(`${BASE_URL}/checkout/cart.jsp`) || href.startsWith(`${BASE_URL}/checkout/orderreview.jsp`)
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('#shopping_bag_header ~ div:first-of-type[class="clr"]'));
	},
};
