import atgCommerce from './_base';

/* Driver description:
- we can apply code on cart and checkout pages
- we can remove code
- we can get existing code
- codes can't replace each other
- Special case:
- after several retesting codes merchant can block us so we need to decrease amount of testing codes
*/

const BASE_URL = 'https://www.heb.com';
const CART_URL = `${BASE_URL}/cart`;
const CHECKOUT_URL = `${BASE_URL}/check-out/payment-details`;

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '#triggerRemoveCode';
		this.APPLIED_CODES_ATTRIBUTES = 'name';
		this.TOTAL_SELECTOR = '#cart_total, #defaultOrderTotal .qty';

		this.APPLY_CODE_FORM_SELECTOR = '#couponForm';
		this.REMOVE_CODE_FORM_SELECTOR = this.APPLY_CODE_FORM_SELECTOR;
		this.REQUEST_PREFIX = '/atg/store/order/purchase/CouponFormHandler';

		this.APPLY_REQUEST_BODY_DATA = {
			'/atg/store/order/purchase/CouponFormHandler.actionType': 'add',
		};
		this.REMOVE_REQUEST_BODY_DATA = {
			'/atg/store/order/purchase/CouponFormHandler.actionType': 'remove',
		};

		this.APPLY_CODE_PARAM_NAME = `${this.REQUEST_PREFIX}.couponCode`;
		this.REMOVE_CODE_PARAM_NAME = `${this.REQUEST_PREFIX}.removeCouponCode`;
	}
};

export default {
	name: 'H-E-B Grocery',
	maxCoupons: 10,
	domain: 'heb.com',
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
	isCartEmpty() {
		return document.querySelector('.red').innerText.match(/There are no items in cart/);
	},
};
