import Promise from 'bluebird';
import atgCommerce from './_base';
import bus from '../../bus';
import { completionActions, log, browserUtils } from '../../../utils';
import { sessionTimer, handleDriverError } from '../../lib';
import { toFloat } from '../../../utils/format';
import { extractElement } from '../../../utils/DOM';

const { wait } = browserUtils;

const DOMAIN_URL = 'https://www.eddiebauer.com';
const BASE_URL = `${DOMAIN_URL}/checkout`;

/** Driver behavior:
	+ Existing code: yes
	+ Remove code: yes
	+ Stackable: no
	+ Free gift support: no
	+ isValid implemented: yes
	+ Show message about free shipping: no
	+ Driver is SPA: yes, rendered total, correct url and not empty cart and spinner not active
	+ Gather merchant codes: yes(main page)
	+ Special case: session timer 13 min.
	+ Pages: bag, payment, review
 * */

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '.success-pro-code';
		this.TOTAL_SELECTOR = '.os-subtotal > .os-detail';
		this.COUPON_FIELD_CLOSED_SELECTOR = '#addPromoCodeClosed';

		this.APPLY_CODE_FORM_SELECTOR = '#apply-promo-code';
		this.REMOVE_CODE_FORM_SELECTOR = '#remove-promo-code';

		this.FREE_SHIPPING_SELECTOR = '.free-ship-sum';
		this.SHIPPING_SELECOTR = '.shipping-rate-link-cart';
		this.TAX_SELECTOR = '.estimated-tax-link';
		this.DONATION_SELECTOR = '.donation-info';
		this.CART_VALUE_SELECTOR = '.right';
		this.APPLY_REQUEST_BODY_DATA = {};
		this.REMOVE_REQUEST_BODY_DATA = {};

		this.APPLY_CODE_PARAM_NAME = '/atg/commerce/promotion/CouponFormHandler.couponClaimCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.APPLY_CODE_INPUT_SELECTOR = '#chkPromoCode';
		this.APPLY_CODE_BTN_SELECTOR = '[data-formid="apply-promo-code"]';
		this.REMOVE_CODE_BTN_SELECTOR = '.remove-promo-code';

		this.EMPTY_ELEMENT1_SELECTOR = '.cart-items-container';
		this.EMPTY_ELEMENT2_SELECTOR = '#emptyCartLinks';

		this.MAX_INTERVAL = 10 * 1000;

		this.MAX_WAIT_TIME = 13;
		this.DOM_CHECK_TIME = 60;
		this.BEFORE_TEST_CODES_DELAY = 3 * 1000;

		this.CORRECT_STEPS = ['bag.jsp', 'payment.jsp', 'review.jsp'];

		this.MERCHANT_CODE_REGEX = /with code: (\w+)/i;
	}

	checkCondition() {
		const { href } = window.location;
		const totalRendered =
			(document.querySelector(this.TOTAL_SELECTOR) || {}).offsetParent && this._parseTotal() !== 0;
		const correctUrl = this.CORRECT_STEPS.some(step => href.includes(step));
		const emptyEl1 = document.querySelector(this.EMPTY_ELEMENT1_SELECTOR);
		const emptyEl2 = document.querySelector(this.EMPTY_ELEMENT2_SELECTOR);
		const isEmptyCart = emptyEl2 || (emptyEl1 && emptyEl1.innerText.match(/bag is currently empty/));
		const spinnerActive = (document.querySelector('.loading-spinner') || {}).offsetParent;

		return correctUrl && totalRendered && !isEmptyCart && !spinnerActive;
	}

	watchForCartUrl() {
		/* The driver turns off running after this.MAX_WAIT_TIME
		of inactivity. Every this.DOM_CHECK_TIME previous state of DOM is compared with the
		current one. In case if it has been changed timer is resetted.
		*/
		sessionTimer.initialize({
			actionInterval: super.watchForCartUrl(),
			timeout: this.MAX_WAIT_TIME,
			checkDOMTime: this.DOM_CHECK_TIME,
			toCheckDOM: true,
		});
	}

	beforeTestCodes() {
		return new Promise(resolve => {
			setTimeout(() => {
				/*
				For control if result of the extension's work is not more than the price before
				running extension. Remove when it is unnecessary. A pause was added to prevent
				getting total price during changing quantity (total is changed after pause). It
				is more relevant for slow internet.
				*/
				resolve();
			}, this.BEFORE_TEST_CODES_DELAY);
		});
	}

	getMerchantCodes() {
		return fetch(DOMAIN_URL, { method: 'GET' })
			.then(res => res.text())
			.then(resHtml => {
				return [resHtml.match(this.MERCHANT_CODE_REGEX)].filter(Boolean).map(item => item[1]);
			});
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(el => el.innerText);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		document.querySelector(this.REMOVE_CODE_BTN_SELECTOR).click();
		return wait(
			() => (document.querySelector(this.COUPON_FIELD_CLOSED_SELECTOR) || {}).offsetParent,
			this.MAX_INTERVAL,
			500,
		).then(() => this._parseTotal());
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Orginal price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code).then(responseJSON => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: false,
				};
				if (responseJSON && responseJSON.success) {
					return this._getUpdatedCart().then(responseHTML => {
						result.finalPrice = this._parseTotal(responseHTML);
						result.finalDiscount = originalPrice - result.finalPrice;
						if (result.finalDiscount > 0) {
							// we need this switch in order to not apply invalid code
							result.isValid = true;
						}
						return this._removeCode(code).then(() => bus.$emit('code-checked', result));
					});
				}
				return bus.$emit('code-checked', result);
			}),
		);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const parseCartValue = selector => {
				const elem = extractElement(selector, response)
					? extractElement(selector, response).parentElement.parentElement.querySelector(
							this.CART_VALUE_SELECTOR,
					  )
					: null;
				return elem ? elem.textContent : 0;
			};
			const total = this._parseTotal(response);
			const shipping = document.querySelector(this.FREE_SHIPPING_SELECTOR)
				? 0
				: toFloat(parseCartValue(this.SHIPPING_SELECOTR)) || 0;
			const tax = toFloat(parseCartValue(this.TAX_SELECTOR)) || 0;
			const donation = toFloat(parseCartValue(this.DONATION_SELECTOR)) || 0;
			return total - shipping - tax - donation;
		});
	}

	async _removeCode(code) {
		// Add to body parametr with code for removing
		if (this.REMOVE_CODE_PARAM_NAME) {
			const codeArray = this.REMOVE_CODE_PARAM_NAME.split(',');
			await this._getRemoveData();
			codeArray.forEach(param => this.removeCodeData.params.set(param, code));
		}

		return this._manageCode(this.removeCodeData, this.REQUEST_HEADERS);
	}

	async _applyCode(code) {
		const codeArray = this.APPLY_CODE_PARAM_NAME.split(',');
		await this._getApplyData();
		codeArray.forEach(param => this.applyCodeData.params.set(param, code));

		return this._manageCode(this.applyCodeData, this.REQUEST_HEADERS);
	}

	_manageCode(bodyData) {
		return fetch(`${bodyData.action}?_=${Math.random()}`, {
			method: 'POST',
			credentials: 'include',
			body: bodyData.params,
		})
			.then(response => response.text())
			.then(response => {
				try {
					return JSON.parse(response);
				} catch (e) {
					handleDriverError(
						new Error('Invalid response', { response, body: JSON.stringify(bodyData.params) }),
					);
				}
				return null;
			});
	}

	_getUpdatedCart() {
		return fetch(`${BASE_URL}/gadgets/bag/bag-order-summary.jsp?_=${Math.random()}`, {
			method: 'GET',
			credentials: 'include',
		})
			.then(response => response.text())
			.then(response => {
				this.lastResponse = response;
				return this.lastResponse;
			});
	}
};

export default {
	name: 'Eddie Bauer',
	domain: 'eddiebauer.com',
	Driver,
	isSinglePageApp: true,
	showDifferentDiscountMessage: true,
	completionAction: completionActions.revisit, // avoid alert
};
