import Promise from 'bluebird';
import atgCommerce from './_base';
import bus from '../../bus';

/* Behaviour
 * can apply code;
 * can remove code;
 * can getExisting code;
 * can't apply multiple codes (not stackable);
 */

const CART_URL = 'https://www.carolina.com/checkout/cart.jsp';

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '#promotional-code';
		this.APPLIED_CODES_ATTRIBUTES = 'value';
		this.TOTAL_SELECTOR = '.total .value';
		this.REMOVE_BTN_ELEMENT = '.promotional-code-remove';

		this.REQUEST_BODY_DATA = new URLSearchParams();

		this.REQUEST_HEADERS = {
			'X-Requested-With': 'XMLHttpRequest',
		};

		this.REQUEST_URL = 'https://www.carolina.com/spc/includes/cartIntermediateRequest.jsp';
		this.REQUEST_CART_URL = 'https://www.carolina.com/spc/includes/displayCartTotals.jsp';
	}

	getExistingCodes() {
		// the code remains in the value attribute after press remove button,
		// so if 'Remove' button has display style 'none' we don't get a code
		const removeButton = document.querySelector(this.REMOVE_BTN_ELEMENT);
		const isCodeRemoved = removeButton.style.display === 'none';
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR)]
			.filter(Boolean)
			.map(elem => (isCodeRemoved ? null : elem.getAttribute('value')))
			.filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0])
			.then(() => this._getUpdatedCart())
			.then(response => this._parseTotal(response));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(() => this._getUpdatedCart())
				.then(response => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
					};

					result.finalPrice = this._parseTotal(response);
					result.finalDiscount = originalPrice - result.finalPrice;

					if (result.finalPrice < originalPrice) {
						return this._removeCode(code).then(() => bus.$emit('code-checked', result));
					}
					return bus.$emit('code-checked', result);
				}),
		);
	}

	_removeCode(code) {
		this.REQUEST_BODY_DATA.set('actionType', 'removeCouponCode');
		return this._manageCode(code);
	}

	_applyCode(code) {
		this.REQUEST_BODY_DATA.set('actionType', 'applyCouponCode');
		return this._manageCode(code);
	}

	_manageCode(code) {
		this.REQUEST_BODY_DATA.set('couponId', code);
		return fetch(`${this.REQUEST_URL}?${this.REQUEST_BODY_DATA.toString()}`, {
			method: 'GET',
			credentials: 'include',
			headers: this.REQUEST_HEADERS,
		});
	}

	_getUpdatedCart() {
		return fetch(`${CART_URL}?_=${Date.now()}`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Carolina',
	domain: 'carolina.com',
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.empty-cart'));
	},
};
