import Promise from 'bluebird';
import atgCommerce from './_base';
import bus from '../../bus';
import { sessionTimer, handleDriverError } from '../../lib';
import { format, DOM, log } from '../../../utils';

const { elemToFloat } = format;
const { extractElement, extractFormValues } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: yes
*/

const BASE_URL = 'https://www.zennioptical.com';
const CART_URL = `${BASE_URL}/shoppingCart`;

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '.btn-remove-promocode';
		this.APPLIED_CODES_ATTRIBUTES = 'data-promo-code';
		this.TOTAL_ELEMENT_CONFIG = { selector: '.order-total' };
		this.TOTAL_SELECTOR = this.TOTAL_ELEMENT_CONFIG.selector;
		this.APPLY_FORM_SELECTOR = '#applyPromotionForm';
		this.REMOVE_FORM_SELECTOR = '#removePromotionForm';
		this.CODE_PROPERTY = '/atg/commerce/order/purchase/CartModifierFormHandler.promotionCode';
		this.ITEM_IN_CART = '.product-list';

		this.REQUEST_URL = `${BASE_URL}/cartridges/ZRAC_ShoppingCart/main_cart.jsp`;

		this.applyData = null;
		this.removeData = null;

		this.PROPOSED_CODES_SELECTOR = '#top-section';
		this.PROPOSED_CODES_REGEX = /code (\w+)/i;

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 30,
		});
	}

	checkCondition() {
		const isItemInCart = document.querySelector(this.ITEM_IN_CART);
		const isOnRightUrl = window.location.href.startsWith(CART_URL);
		const isTotal = document.querySelector(this.TOTAL_ELEMENT_CONFIG.selector);
		return isItemInCart && isOnRightUrl && isTotal;
	}

	beforeTestCodes() {
		this.applyData = extractFormValues(this.APPLY_FORM_SELECTOR);
		this.removeData = extractFormValues(this.REMOVE_FORM_SELECTOR);
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.PROPOSED_CODES_SELECTOR)]
			.map(el => el.textContent.match(this.PROPOSED_CODES_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this._removeCode(code))
			.then(() => this._getUpdatedCart())
			.then(responseHTML => this._parseTotal(responseHTML));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);

		const workingDiscounts = [];

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
			};
			return this._applyCode(code).then(responseJSON => {
				if (!responseJSON || !responseJSON.success) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return this._getUpdatedCart().then(responseHTML => {
					result.finalPrice = this._parseTotal(responseHTML);
					result.finalDiscount = originalPrice - result.finalPrice;
					if (result.finalPrice < originalPrice) {
						workingDiscounts.push(result);
					}

					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			});
		}).then(() =>
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
			}),
		);
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice } = validationInfo;

		discounts.sort((a, b) => b.finalDiscount - a.finalDiscount);

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];

		return Promise.each(discounts, discount =>
			this._applyCode(discount.code)
				.then(() => this._getUpdatedCart())
				.then(responseHTML => {
					const finalPrice = this._parseTotal(responseHTML);
					const finalDiscount = originalPrice - finalPrice;

					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
					});

					if (finalPrice < currentPrice) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						return Promise.resolve();
					}
					return this._removeCode(discount.code);
				}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			}),
		);
	}

	getPreTaxShippingTotal() {
		return this._getCartPage().then(response => this._parseTotal(response));
	}

	_removeCode(code) {
		return this._manageCode(code, this.removeData.params);
	}

	_applyCode(code) {
		return this._manageCode(code, this.applyData.params);
	}

	_manageCode(code, data) {
		data.set(this.CODE_PROPERTY, code);

		return this._makeRequest('POST', data, true);
	}

	_getUpdatedCart(retry = false) {
		return this._makeRequest().then(responseHTML => {
			// retry for total element
			if (!extractElement(this.TOTAL_SELECTOR, responseHTML) && !retry) {
				return this._getUpdatedCart(true);
			}
			return responseHTML;
		});
	}

	_getCartPage() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_makeRequest(method = 'GET', body = null, json = false) {
		let responseData;
		return fetch(this.REQUEST_URL, {
			method,
			credentials: 'include',
			body,
		})
			.then(response => {
				responseData = response;
				return response.text();
			})
			.then(response => {
				if (json) {
					try {
						return JSON.parse(response);
					} catch (e) {
						const { status, redirected, statusText, headers } = responseData;
						const errorPayload = {
							status,
							redirected,
							headers,
							statusText,
							textContent: response.trim().slice(0, 5000),
						};
						// we send errorPayload in sentry for further investigation
						handleDriverError(new Error('Invalid json string'), errorPayload);
						return null;
					}
				}
				return response;
			});
	}

	_parseTotal(data = false) {
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
	}
};

export default {
	name: 'Zenni Optical',
	domain: 'zennioptical.com',
	Driver,
	stackable: true,
	isSinglePageApp: true,
};
