import atgCommerce from './_base';
import sendXHR from '../../../utils/send-xhr';
import { elemToFloat } from '../../../utils/format';
import { extractElement } from '../../../utils/DOM';

/**
 * Driver behavior description:
 -    Existing code: yes
 -    Remove code: We can not remove code, but we can remove code by appling ' '
 -    Stackable: no
 -    Free gift support: no
 -    ‘isValid’ implemented: yes
 Optional:
 -    Show message about free shipping on the cart page (without discount info): no
 -    Is driver SPA and add the condition to show popup: no
 -	  code testing on the cart and checkout pages
 -	  We can replace valid code by invalid and valid codes on checkout page
 */

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '#removeCoupon, #applyCoupon, #cartCouponBox';
		this.APPLIED_CODES_REGEXP = /Coupon\s(\w*)\s/;
		this.TOTAL_SELECTOR =
			'td:last-child #orderSummaryTotal, #orderSummaryTable tr:last-of-type td:last-of-type span';

		this.APPLY_CODE_FORM_SELECTOR = '#applyCoupon, #singleColumn>form';
		this.REMOVE_CODE_FORM_SELECTOR = this.APPLY_CODE_FORM_SELECTOR;

		this.APPLY_REQUEST_BODY_DATA = {
			'/atg/commerce/promotion/CouponFormHandler.claimCoupon.x': '37',
			'/atg/commerce/promotion/CouponFormHandler.claimCoupon.y': '16',
		};
		this.REMOVE_REQUEST_BODY_DATA = this.APPLY_REQUEST_BODY_DATA;

		this.APPLY_CODE_PARAM_NAME = '/atg/commerce/promotion/CouponFormHandler.couponClaimCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.REQUEST_HEADERS = {
			accept: 'text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8',
			'content-type': 'application/x-www-form-urlencoded',
		};

		this.CHARGES_REGEX = /(shipping|tax)/i;
		this.CHARGES_SELECTOR = '#orderSummaryTable td:last-child, #cartSubTotalBox td:last-child';
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(code =>
				code.innerText.trim().match(this.APPLIED_CODES_REGEXP)
					? code.innerText.trim().match(this.APPLIED_CODES_REGEXP)[1]
					: null,
			)
			.filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this._removeCode(' ').then(response => this._parseTotal(response));
	}

	// in FF request on cart page can be send only by sendXHR
	// but in Chrome only with fetch
	// that is why we need to detect in which driver user run Genie
	_manageCode(bodyData, headersData) {
		const requestSender = navigator.userAgent.includes('Firefox') ? sendXHR : fetch;
		return requestSender('https://www.calendars.com/checkout/billing.jsp?_DARGS=/checkout/billing.jsp', {
			method: 'POST',
			credentials: 'include',
			body: bodyData.params,
			headers: headersData,
		}).then(response => response.text());
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]).then(() =>
			window.history.replaceState(
				window.history.state,
				window.document.title,
				window.location.href.split('?')[0],
			),
		);
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this._getUpdatedCart();
		const total = this._parseTotal(cartStr);
		const taxes = [...extractElement(this.CHARGES_SELECTOR, cartStr.summary, true)]
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(elemToFloat)
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0); // shipping & tax might not be counted on cart
		return total - taxes;
	}

	_getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Сalendars',
	domain: 'calendars.com',
	Driver,
	cartContinueUrl: window.location.href.split('?')[0],
	isOnCartUrl(href) {
		return href.match(/(http|https):\/\/www\.calendars\.com\/checkout\/(cart|order_review)\.jsp/);
	},
	isCartEmpty() {
		return document.querySelector(
			'#cartItemsTable td:only-child, #zeroOrder, #orderSummaryTable tbody tr:nth-child(2) > td:first-child > span:first-child',
		);
	},
};
