import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { DOM, format, log } from '../../../utils';
import { sessionTimer } from '../../lib';

const { toFloat, elemToFloat } = format;
const { extractFormValues, extractElement } = DOM;

/* Driver behavior description:
•    Existing code: yes
•    Remove code: no
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: no
•    maxCoupons: 10
•    code testing may be run on 'Cart' and 'Payment' pages;
Optional:
•    There are codes, that can replace another codes
•    Show message about free shipping on the cart page (without discount info): no
•    Is driver SPA and add the condition to show popup: yes
•    Gather codes on site pages: no
======== Special case ========
we can't remove codes, so there is no need to stack codes */

const BASE_URL = 'https://www.lastcall.com/checkout';
const CART_URL = `${BASE_URL}/cart.jsp`;
const IS_ON_CART = window.location.href.startsWith(CART_URL);

const Driver = class Driver extends BaseDriver {
	constructor() {
		super();
		this.APPLIED_CODES_SELECTOR = IS_ON_CART ? '.ordercharges .promo.desc.code' : '.ordercharges .promo.desc';
		this.TOTAL_SELECTOR = '#txt_orderTotal';
		this.BAD_PROMO_SELECTOR = '.BadPromoCode';
		this.CODE_REGEX = IS_ON_CART ? /(.*)/ : /Promotion[s]? Applied(.*)/;

		this.MERCHANT_CODES_SELECTOR = '.promo-container .promo-text span:last-child, span.coupon';

		this.SHIPPING_SELECTOR = '#txt_deliveryAndProcessingCharges';
		this.TAX_SELECTOR = '#txt_taxAmount';

		this.SUBMIT_ELEMENT_SELECTOR = '#promo-form [name="/nm/formhandler/checkout/CartFormHandler.applyPromoCode"]';
		this.FORM_SELECTOR = '#promo-form';

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const rightUrl = href.startsWith(CART_URL) || href.startsWith(`${BASE_URL}/orderreview.jsp`);
		const totalElem = document.querySelector(this.TOTAL_SELECTOR);
		const isTotal = totalElem ? toFloat(totalElem.textContent) !== 0 : false;
		const currentState = rightUrl && isTotal;
		return currentState;
	}

	getMerchantCodes() {
		return [...new Set([...document.querySelectorAll(this.MERCHANT_CODES_SELECTOR)].map(el => el.textContent))];
	}

	getExistingCodes(data) {
		const codesElement = extractElement(this.APPLIED_CODES_SELECTOR, data);
		let codes = [];
		if (codesElement) {
			codes = codesElement.innerText
				.match(this.CODE_REGEX)[1]
				.split(',')
				.map(code => code.replace(':', '').trim())
				.filter(Boolean);
		}
		this.existingCodes = codes;

		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeTestCodes() {
		this.requestData = extractFormValues(this.FORM_SELECTOR);
		const submitInputElement = document.querySelector(this.SUBMIT_ELEMENT_SELECTOR);
		if (this.requestData && submitInputElement) {
			// `FormData()` in `extractFormValues()` does not grab input with submit type
			this.requestData.params.set(
				'/nm/formhandler/checkout/CartFormHandler.applyPromoCode',
				submitInputElement.value,
			);
		}
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);
		let currentPrice = originalPrice;
		let workingCodes = this.existingCodes;

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
			};
			return this._applyCode(code, this.requestData)
				.then(() => this._getUpdatedCart())
				.then(html => {
					const currentAppliedCodes = this.getExistingCodes(html).codes;
					if (
						extractElement(this.BAD_PROMO_SELECTOR, html) ||
						(currentAppliedCodes.length && !currentAppliedCodes.includes(code)) // check if current code is on the page
					) {
						return bus.$emit('code-checked', result);
					}
					result.finalPrice = this._parseTotal(html);
					result.finalDiscount = originalPrice - result.finalPrice;

					// if currentCode increases price, we apply codes that were applied before
					if (result.finalPrice > currentPrice) {
						return Promise.each(workingCodes, workingCode => {
							if (currentAppliedCodes.includes(workingCode)) {
								return Promise.resolve();
							}
							return this._applyCode(workingCode, this.requestData);
						}).then(() => bus.$emit('code-checked', result));
					}
					currentPrice = result.finalPrice;
					workingCodes = currentAppliedCodes;

					return bus.$emit('code-checked', result);
				});
		}).then(() => {
			// we can't remove codes, so they are already applied

			return bus.$emit('stack-codes', {
				codes: workingCodes,
				finalPrice: currentPrice,
				finalDiscount: originalPrice - currentPrice,
			});
		});
	}

	stackCodes(stackedCodes) {
		// we can't remove codes, so they are already applied
		return bus.$emit('stackable-complete', stackedCodes);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0], this.requestData);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(html => {
			const total = this._parseTotal(html);

			const shippingElement = extractElement(this.SHIPPING_SELECTOR, html);
			const shipping = shippingElement ? elemToFloat(shippingElement) || 0 : 0;

			const taxElement = extractElement(this.TAX_SELECTOR, html);
			const tax = taxElement ? elemToFloat(taxElement) || 0 : 0;

			return total - shipping - tax;
		});
	}

	_applyCode(code, requestData) {
		requestData.params.set('/nm/formhandler/checkout/CartFormHandler.promoCode', code);
		return fetch(requestData.action, {
			method: 'POST',
			body: requestData.params,
			credentials: 'include',
			redirect: 'manual',
			headers: {
				'X-Requested-With': 'XMLHttpRequest',
			},
		});
	}

	_getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_parseTotal(data = false) {
		const totalElement = extractElement(this.TOTAL_SELECTOR, data).textContent.trim();
		// Price format can be different: '$126,00', '$126.00' and '$2,123.00'
		return toFloat(totalElement.length < 9 ? totalElement.replace(',', '.') : totalElement.replace(',', ''));
	}
};

export default {
	name: 'Last Call',
	domain: 'lastcall.com',
	Driver,
	stackable: true,
	isSinglePageApp: true,
	maxCoupons: 10, // may block
};
