import Promise from 'bluebird';
import atgCommerce from './_base';
import bus from '../../bus';
import { format, DOM, log } from '../../../utils';

const { elemToFloat } = format;
const { extractElement, extractFormValues } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: yes, on Cart page from the Available coupons block
*/

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '.removeCouponBtn';
		this.APPLIED_CODES_ATTRIBUTES = 'data-id';
		this.TOTAL_SELECTOR = '.estTotals .cartSummaryAmounts';
		this.MERCHANT_CODE_SELECTOR = '.applyCouponBtn';

		this.SHIPPING_SELECTOR = '#shippingAmount';
		this.TAX_SELECTOR = '#taxDetail .cartSummaryAmounts';

		this.APPLY_CODE_FORM_SELECTOR = '#promoCodeForm';
		this.REMOVE_CODE_FORM_SELECTOR = '#removeCouponForm';
		this.APPLY_CODE_PARAM_NAME = 'promoCodeEntry';
		this.REMOVE_CODE_PARAM_NAME = '/gdi/ui/handler/RemoveCouponFormHandler.couponCode';
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.MERCHANT_CODE_SELECTOR)].map(code =>
			code.getAttribute(this.APPLIED_CODES_ATTRIBUTES),
		);
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Orginal price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code).then(response => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
				};
				// if no error may be 'error: "false"'
				if (response.error === 'true') {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				result.finalPrice = this._parseTotal(response);
				result.finalDiscount = originalPrice - result.finalPrice;
				return this._removeCode(code).then(() => bus.$emit('code-checked', result));
			}),
		);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedPage().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const taxEl = extractElement(this.TAX_SELECTOR, response);
			const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
			const total = elemToFloat(extractElement(this.TOTAL_SELECTOR, response));
			return total - shipping - tax;
		});
	}

	_getRemoveData() {
		if (!this.removeCodeData) {
			this.removeCodeData = extractFormValues(this.REMOVE_CODE_FORM_SELECTOR);
		}
		return this.removeCodeData;
	}

	_getApplyData() {
		if (!this.applyCodeData) {
			this.applyCodeData = extractFormValues(this.APPLY_CODE_FORM_SELECTOR);
		}
		return this.applyCodeData;
	}

	_getUpdatedPage() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(res => res.text());
	}

	_parseTotal(data) {
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data ? data.cartSummary : null));
	}
};

export default {
	name: 'Garage Clothing',
	domain: 'garageclothing.com',
	Driver,
	isOnCartUrl(href) {
		return (
			href.startsWith('https://www.garageclothing.com/us/checkout/cart.jsp') &&
			document.querySelector('#promoCodeEntry')
		);
	},
};
