import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { completionActions, format, DOM } from '../../../utils';
import { sessionTimer } from '../../lib';

const { elemToFloat, toFloat } = format;
const { extractFormValues, setValue } = DOM;

const BASE_URL = 'https://www.competitivecyclist.com';
const IS_ON_CHECKOUT = window.location.href.includes('checkout');
const TOTAL_SELECTOR = '#order-total';

/* Driver behavior description:
•    Existing code: no
•    Remove code: no
•    Stackable: no
•    Free gift support: no
•    ‘isValid’ implemented: yes
•    Manually apply code: yes
•    sessionTimer: 15min
•    Active pages: Cart, Checkout
Optional:
•    Show message about free shipping on the cart page: no
•    Is driver SPA and add the condition to show popup: no
•    Gather codes on site pages: no */

const Driver = class Driver extends BaseDriver {
	constructor() {
		super();
		this.FORM_SELECTOR = '#ajaxform-redeemCoupon';
		this.SHIPPING_OPTIONS_SELECTOR = '[id^="ship-method-"]';
		this.PROMO_CODE_SELECTOR = '#promo-code';
		this.APPLY_BUTTON_SELECTOR = '#redeem-code';
		this.MERCHAN_CODES_SELECTOR = '.js-header-global-text-promo';
		this.MERCHANT_CODES_REGEX = /use code\s+(.*)/i;
		this.COUPON_LINK_SELECTOR = '#coupon-redemption-link';
		this.BILLING_ZIPCODE = '#billing-anon-zipcode';
		this.BILLING_COUNTRY = '#billing-anon-country';
		this.BILLING_STATE = '#billing-anon-state-province';
		this.BILLING_CITY = '#billing-anon-city';
		this.BILLING_ADDRESS = '#billing-anon-address1';
		this.SHIPPING_ZIPCODE = '#shipping-anon-zipcode';
		this.SHIPPING_COUNTRY = '#shipping-anon-country';
		this.SHIPPING_STATE = '#shipping-anon-state-province';
		this.SHIPPING_CITY = '#shipping-anon-city';
		this.SHIPPING_ADDRESS = '#shipping-anon-address1';
		this.SAME_ADDRESS_SELECTOR = '#same-address';
		this.EMPTY_CART_SELECTOR = '#empty-cart';

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const correctURL =
			(href.startsWith(`${BASE_URL}/Store/checkout/checkout.jsp`) ||
				href.startsWith(`${BASE_URL}/Store/cart/cart.jsp`)) &&
			!href.includes('wishlist');
		const isEmptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		return correctURL && !isEmptyCart;
	}

	beforeTestCodes() {
		this.applyRequestData = extractFormValues(this.FORM_SELECTOR);
		if (this.applyRequestData) {
			this.applyRequestData.params.set('/atg/commerce/promotion/CouponFormHandler.claimCoupon', '');
		}

		if (IS_ON_CHECKOUT) {
			const shipMethod = this._getShipMethod();
			const billPostalCode = document.querySelector(this.BILLING_ZIPCODE).value;
			const billCountry = document.querySelector(this.BILLING_COUNTRY).value;
			const billState = document.querySelector(this.BILLING_STATE).value;
			const billCity = document.querySelector(this.BILLING_CITY).value;
			this.taxRequestParams = new URLSearchParams({
				computeTax: 'true',
				shipMethod,
				billPostalCode,
				billCountry,
				billState,
				billCity,
			});
			if (document.querySelector(this.SAME_ADDRESS_SELECTOR).checked) {
				this.taxRequestParams.set('shipPostalCode', billPostalCode);
				this.taxRequestParams.set('shipCountry', billCountry);
				this.taxRequestParams.set('shipState', billState);
				this.taxRequestParams.set('shipCity', billCity);
				this.taxRequestParams.set('shipAddress1', document.querySelector(this.BILLING_ADDRESS).value);
			} else {
				this.taxRequestParams.set('shipPostalCode', document.querySelector(this.SHIPPING_ZIPCODE).value);
				this.taxRequestParams.set('shipCountry', document.querySelector(this.SHIPPING_COUNTRY).value);
				this.taxRequestParams.set('shipState', document.querySelector(this.SHIPPING_STATE).value);
				this.taxRequestParams.set('shipCity', document.querySelector(this.SHIPPING_CITY).value);
				this.taxRequestParams.set('shipAddress1', document.querySelector(this.SHIPPING_ADDRESS).value);
			}
		}
	}

	getMerchantCodes() {
		return [document.querySelector(this.MERCHANT_CODES_SELECTOR)]
			.filter(Boolean)
			.map(el => el.textContent.match(this.MERCHANT_CODES_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this.applyCode(code).then(applyCodeResponse => {
				if (applyCodeResponse.errorMessage || !applyCodeResponse.success) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				return this._updateOrderDetails().then(updatedOrder => {
					result.finalPrice = this._parseTotal(updatedOrder);
					result.finalDiscount = originalPrice - result.finalPrice;

					if (result.finalDiscount <= 0) {
						result.isValid = false;
						return bus.$emit('code-checked', result);
					}
					// valid codes replace each other
					return bus.$emit('code-checked', result);
				});
			});
		});
	}

	applyCodes(codes) {
		return IS_ON_CHECKOUT ? this.applyCodeManually(codes[0]) : this.applyCode(codes[0]);
	}

	applyCode(code) {
		if (!IS_ON_CHECKOUT) {
			return fetch(`${BASE_URL}/Store/services/coupon/${code}`, {
				method: 'POST',
				credentials: 'include',
				headers: {
					'X-Requested-With': 'XMLHttpRequest',
				},
			}).then(response => response.json());
		}
		this.applyRequestData.params.set('/atg/commerce/promotion/CouponFormHandler.couponClaimCode', code);
		return fetch(this.applyRequestData.action, {
			method: 'POST',
			credentials: 'include',
			body: this.applyRequestData.params,
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
				'X-Requested-With': 'XMLHttpRequest',
			},
		}).then(response => response.json());
	}

	_updateOrderDetails() {
		let requestURL = `${BASE_URL}/Store/cart/currentOrderDetails.json.jsp?_=${Date.now()}`;
		if (IS_ON_CHECKOUT) {
			requestURL = `${BASE_URL}/Store/cart/currentOrderDetails.json.jsp?${this.taxRequestParams.toString()}`;
		}
		return fetch(requestURL, {
			method: 'GET',
			credentials: 'include',
			headers: {
				'X-Requested-With': 'XMLHttpRequest',
			},
		}).then(response => response.json());
	}

	getPreTaxShippingTotal() {
		return this._updateOrderDetails().then(response => {
			const total = toFloat(response.attributes.orderTotal);
			const tax = toFloat(response.attributes.tax || 0);
			const shipping = toFloat(response.attributes.shipping) || 0;
			return total - shipping - tax;
		});
	}

	_parseTotal(dataJSON) {
		if (dataJSON) {
			return toFloat(dataJSON.attributes.orderTotal);
		}
		return elemToFloat(document.querySelector(TOTAL_SELECTOR));
	}

	applyCodeManually(code) {
		document.querySelector(this.COUPON_LINK_SELECTOR).click();
		setValue(document.querySelector(this.PROMO_CODE_SELECTOR), code);
		document.querySelector(this.APPLY_BUTTON_SELECTOR).click();
	}

	_getShipMethod() {
		// Default standart shipping
		const defaultShippingMethod = 'USTANDARD';
		const shippingInputs = Array.from(document.querySelectorAll(this.SHIPPING_OPTIONS_SELECTOR));
		const checkedInput = shippingInputs.find(input => input.checked);
		return checkedInput ? checkedInput.value : defaultShippingMethod;
	}
};

export default {
	name: 'Competitive Cyclist',
	domain: 'competitivecyclist.com',
	completionAction: IS_ON_CHECKOUT ? completionActions.none : completionActions.reload,
	isSinglePageApp: true,
	Driver,
};
