import atgCommerce from './_base';

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Payment step(https://www.nutrisystem.com/checkout/billing.jsp)
+ Is driver SPA: no
+ Gather codes on site pages: yes
+ credentials: Email: dskfjhdsajkdfh@yahoo.com, Password: tH6yP4FWWF3Zdjq
+ maxCoupons: 8, due to slow code testing
*/

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '.promo-table td:last-child a';
		this.APPLIED_CODES_ATTRIBUTES = 'id';
		this.TOTAL_SELECTOR = '.totals .price';

		this.APPLY_CODE_FORM_SELECTOR = '[name=checkoutPromo]';
		this.REMOVE_CODE_FORM_SELECTOR = '[name=orderPromoSummary]';

		this.APPLY_REQUEST_BODY_DATA = {
			'/nutrisystem/order/CouponGiftCertificateHandler.applyCoupon': '',
		};
		this.REMOVE_REQUEST_BODY_DATA = {
			'/nutrisystem/order/CouponGiftCertificateHandler.removePromoCoupon': 'Submit',
		};

		this.APPLY_CODE_PARAM_NAME = 'promo-code';
		this.REMOVE_CODE_PARAM_NAME = '/nutrisystem/order/CouponGiftCertificateHandler.removeCouponId';

		this.REQUEST_HEADERS = {
			Accept: 'text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8',
			'Content-Type': 'application/x-www-form-urlencoded',
		};

		this.isStackable = true;
		this.PROPOSED_CODES_SELECTOR = '.bx-row-coupon div';
	}

	getMerchantCodes() {
		return [
			...new Set(
				[...document.querySelectorAll(this.PROPOSED_CODES_SELECTOR)].map(el => el.textContent).filter(Boolean),
			),
		];
	}
};

export default {
	name: 'nutriSystem',
	domain: 'nutrisystem.com',
	stackable: true,
	Driver,
	maxCoupons: 8,
	isOnCartUrl(href) {
		return href.startsWith('https://www.nutrisystem.com/checkout/billing.jsp');
	},
};
