/*    eslint class-methods-use-this: ["error", { "exceptMethods": ["_codeAppliedCheck", "_getUpdatedCart", "_manageCode"] }] */
import Promise from 'bluebird';
import atgCommerce from './_base';
import bus from '../../bus';

import { format, DOM, log } from '../../../utils';
import { sessionTimer } from '../../lib';
import { extractData } from '../../../utils/DOM';
import { toFloat } from '../../../utils/format';

const { elemToFloat } = format;
const { extractFormValues, extractElement } = DOM;

const BASE_URL = 'https://www.michaelkors.com';

/*
Driver behaviour description:
    Remove code: yes
    Existing code: yes
    Stackable: ? (only one code exists)
    Free gift support: no
    'isValid' implemented: yes
    Is driver SPA and add the condition to show popup: yes
	sessionTimer: 15 min
	run testing on the cart and checkout(2 & 3 steps) pages.
	On the checkout after reloading page user wil be redirected to the 1 step, but his data will be saved
*/

const Driver = class Driver extends atgCommerce {
	constructor() {
		super();

		this.APPLIED_CODES_SELECTOR = '.removeFromCoupons';
		this.APPLIED_CODES_REGEXP = /couponId=([0-9A-Za-z]*)/;
		this.TOTAL_SELECTOR = '.product-values .total:first-child, #summary .text-right .font-ordersummary-total';
		this.REMOVE_PARAMETR_SELECTOR = '#couponAppliedFromPayment';
		this.APPLY_CODE_FORM_SELECTOR = '#orderPromotionForm';

		this.APPLY_REQUEST_URL = `${BASE_URL}/checkout/common/ajaxFormSubmit.jsp`;
		this.DATA_SUMMARY_URL = `${BASE_URL}/checkout/includes/shoppingCartInclude.jsp`;

		this.INPUT_CODE_SELECTOR = '#orderPromotionForm #shopping-bag-promo';
		this.PROMO_SECTION_SELECTOR = '[href="#cartPromotion"], [href="#checkoutPromotion"]';
		this.SHIPPING_SELECTOR = '#shippingContainer';

		this.SHIPPING_AND_TAX_SELECTOR = window.location.href.includes('shoppingCart')
			? '.shopping-bag-order-summary-details li .pull-right'
			: '#order-summary li:nth-of-type(2) .pull-right, #order-summary li:nth-of-type(3) .pull-right, #order-summary li:nth-of-type(4) .pull-right';

		sessionTimer.initialize();
	}

	checkCondition() {
		const { href } = window.location;

		const cartURL =
			href.startsWith(`${BASE_URL}/checkout/shoppingCart.jsp`) &&
			document.querySelector(this.INPUT_CODE_SELECTOR);
		const checkoutURL =
			href.startsWith(`${BASE_URL}/checkout/checkout.jsp`) &&
			(document.querySelector(this.SHIPPING_SELECTOR) &&
				document.querySelector(this.SHIPPING_SELECTOR).classList.contains('complete'));

		return checkoutURL || cartURL;
	}

	getExistingCodes() {
		const aplliedCodeEl = document.querySelector(this.APPLIED_CODES_SELECTOR);
		const codes = aplliedCodeEl ? [aplliedCodeEl.getAttribute('href').match(this.APPLIED_CODES_REGEXP)[1]] : [];

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		document.querySelector(this.APPLIED_CODES_SELECTOR).click();
		document.querySelector(this.PROMO_SECTION_SELECTOR).click();

		return Promise.delay(3000)
			.then(() => this._dataSummary())
			.then(responseText => this._parseTotal(responseText));
	}

	beforeCheckCodes() {
		this.APPLY_BODY = extractFormValues(this.APPLY_CODE_FORM_SELECTOR);
		return Promise.resolve();
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this._applyCode(code).then(response => {
				if (response.status === 403) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				return response
					.clone()
					.json()
					.catch(() => response.text())
					.then(responseJSON => {
						if (this._codeAppliedCheck(responseJSON)) {
							return this._dataSummary().then(responseHTML => {
								result.finalPrice = this._parseTotal(responseHTML);
								result.finalDiscount = originalPrice - result.finalPrice;

								return this._removeCode(responseHTML).then(() => bus.$emit('code-checked', result));
							});
						}
						result.isValid = false;
						return bus.$emit('code-checked', result);
					});
			});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const total = this._parseTotal(responseHTML);
			const data = [...extractData(this.SHIPPING_AND_TAX_SELECTOR, responseHTML, true)].map(el => {
				return el.includes('--') ? 0 : toFloat(el);
			});
			const taxData = data.reduce((a, c) => a + c);
			return total - taxData;
		});
	}

	_getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_codeAppliedCheck(data) {
		return data.result !== 'error' || !data.errors;
	}

	_dataSummary() {
		return this._manageCode(this.DATA_SUMMARY_URL, 'GET').then(response => response.text());
	}

	_applyCode(code) {
		this.APPLY_BODY.params.set('couponCode', code);
		this.APPLY_BODY.params.set('formName', 'orderPromotionForm');

		return this._manageCode(this.APPLY_REQUEST_URL, 'POST', this.APPLY_BODY.params);
	}

	_removeCode(data = false) {
		const removeUrl = extractElement(this.APPLIED_CODES_SELECTOR, data).getAttribute('href');
		const couponAppliedFromPayment = extractData(this.REMOVE_PARAMETR_SELECTOR, false, 'value');
		return this._manageCode(`${BASE_URL}${removeUrl}&couponAppliedFromPayment=${couponAppliedFromPayment}`, 'POST');
	}

	_manageCode(url, method, body) {
		return fetch(url, {
			method,
			credentials: 'include',
			headers: {
				'x-requested-with': 'XMLHttpRequest',
			},
			body,
		}).then(response => response);
	}

	_parseTotal(data = false) {
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
	}
};

export default {
	name: 'Michael Kors',
	domain: 'michaelkors.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 3, // site block us after 5 requests
};
